* {
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
}

.container {
  height: 100vh;
  padding: 0 20px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(2, 300px);
  align-items: center;
  gap: 20px;
}

.block_first {}

.block_second {
  border: 1px solid black;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.gamers {
  display: flex;
  align-items: center;
  gap: 40px;
  margin-bottom: 40px;
}

.play_ground {
  /* border: 1px solid black; */
  height: 300px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border: 0.5px solid black;
}

.play_ground .slot {
  border: 0.5px solid black;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  position: relative;
  cursor: pointer;
}

.play_ground .slot:hover {
  background: #00000020;
}

.play_ground .slot div {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gamer.active {
  color: red;
  transition: all 0.3s;
}

.gamers.hidden {
  display: none;
}

.winners h3 {
  display: none;
  font-size: 30px;
  color: rgb(78, 78, 223);
}

.winners h3.active {
  display: block;
  margin-bottom: 40px;
}

.btn {
  background: rgb(242, 138, 52);
  color: white;
  padding: 20px;
  text-align: center;
  cursor: pointer;
  border-radius: 4px;
  margin: 20px 0;
}

@media (max-width:700px) {
  .wrapper {
    grid-template-columns: repeat(1, 300px);
  }
}